<template>
    <transition name="jc-slide-bottom">
        <div v-show="value" :class="b({ 'withtitle': title })">
            <div v-show="title" class="jc-hairline--top-bottom" :class="b('header')">
                <div v-text="title" />
            </div>
            <ul class="jc-hairline--bottom">
                <li v-for="item in actions" :class="[b('item', { disabled: item.disabled }), item.className, 'jc-hairline--top']" @click.stop="onSelect(item)" :key="item.key">
                    <template>
                        <span :class="b('name')">{{ item.name }}</span>
                        <span :class="b('subname')" v-if="item.subname">{{ item.subname }}</span>
                    </template>
                </li>
            </ul>
            <div v-text="cancelText" :class="[b('cancel'), 'jc-hairline--top']" @click="onCancel" />
        </div>
    </transition>
</template>
<script>
import create from "utils/create";
import PopUp from "mixins/popup";
export default create({
    name: "actionsheet",
    mixins: [PopUp],
    props: {
        value: Boolean,
        title: String,
        cancelText: String,
        actions: {
            type: Array,
            default: () => []
        },
        overlay: {
            type: Boolean,
            default: true
        },
        closeOnClickOverlay: {
            type: Boolean,
            default: true
        }
    },
    methods: {
        onSelect(item) {
            if (!item.disabled && !item.loading) {
                item.callback && item.callback(item);
                this.$emit("select", item);
            }
        },
        onCancel() {
            this.$emit("input", false);
            this.$emit("cancel");
        }
    }
});
</script>