<template>
    <transition name="jc-dialog-bounce">
      <div :class="b()" v-show="value">
        <div v-if="title" v-text="title" :class="b('header', { isolated: !message && !$slots.default })" ></div>
        <div :class="b('content')" v-if="message || $slots.default">
          <slot>
            <div v-if="message" v-html="message" :class="b('message')" />
          </slot>
      </div>
      <div :class="b('toolbar')">
        <jc-button v-if="showCancel" :full="true" @click="handle('cancel')">取消</jc-button>
        <jc-button :full="true" :type="'primary'" @click="handle('confirm')">确定</jc-button>
      </div>    
      </div>
    </transition>
</template>

<script>
import create from 'utils/create.js';
import PopUp from "mixins/popup"; 
import JcButton from "packages/button";
export default create({
  //组件名称
    name: "dialog",
    //组件将不会把未被注册的props呈现为普通的HTML属性
    inheritAttrs: false,
    mixins: [PopUp],
    props: {
        title: String,
        hover: {
          type: Boolean,
          default: false
        },
        message: String,
        callback: Function,
        beforeClose: Function,
        confirmButtonText: String,
        cancelButtonText: String,
        showCancel: Boolean,
        showConfirm: {
          type: Boolean,
          default: false
        },
        modal: {
          type: Boolean,
          default: false
        },
        closeOnClickOverlay: {
          type: Boolean,
          default: false
        }
    },
    components: {
    JcButton
  },
    methods: {
      handle(action){
        this.$emit('input', false);
        this.onClose(action);
      },
      onClose(action) {
        this.$emit('input', false);
        this.callback && this.callback(action);
      }
    }
    
})
</script>
