<template>
  <div  :class="b()">
    <ul :class="b('content')" ref="content" :style="{ 'height': currentHeight + 'px'}">
        <li :class="b('index-section')" v-for="(section,index) in sections" :id="'view'+index" :ref="'view'+index" :key="index">
          <div :class="b('index-section',['index'])">{{index}}</div>
          <div v-for="(item,i) in section">
            <slot>
              <jc-cell v-if="!checkbox" :title="item">
                <img slot="icon" src="@/assets/mobile/cu-pic2.jpg" />
              </jc-cell>
              <jc-checklist-group v-if="checkbox" v-model="checks" @change="change">
                <jc-checklist :name="item">
                  <jc-cell :title="item">
                    <img slot="icon" src="@/assets/mobile/cu-pic2.jpg" />
                  </jc-cell>
                </jc-checklist>
              </jc-checklist-group>
            </slot>
          </div>
        </li>
    </ul>
    <div :class="b('nav')" @touchstart="handleTouchStart" ref="nav">
        <ul :class="b('navlist')">
            <li :class="b('navitem')" v-for="(item,index) in sections" :key="index">{{ index }}</li>
        </ul>
    </div>
  </div>
</template>
<script type="text/babel">
  import create from "utils/create";
  import cnToPinyin from "./pinyinUtil";
  export default create({
    name: 'indexlist',

    props: {
      height: Number,
      showToast: {
        type: Boolean,
        default: true
      },
      checkbox: {
        type: Boolean,
        default: true
      },
      staff: Array,
      duration: {
        type: Number,
        default() {
          return 500;
        }
      }
    },

    data() {
      return {
        sections: {},
        currentHeight: this.height,
        navOffsetX: 0,
        checks:[]
      };
    },

    watch: {
      height(val) {
        if (val) {
          this.currentHeight = val;
        }
      }
    },

    methods: {
      change() {
        this.$emit('change',this.checks);
      },
      getStaffArray(staff) {
        let arr = {}
        staff.sort((a,b)=>{
          return Number(cnToPinyin.getFirstLetter(a).charCodeAt(0)) - Number(cnToPinyin.getFirstLetter(b).charCodeAt(0)) 
        })
        staff.forEach((item)=>{
            if(!arr[cnToPinyin.getFirstLetter(item)[0]]) {
              arr[cnToPinyin.getFirstLetter(item)[0]] = []
            }
            arr[cnToPinyin.getFirstLetter(item)[0]].push(item)
          })
        return arr;
      },
      
      sort(staff) {
        if(staff.every((item)=>{
          return typeof item === 'string' || 'object'
        })) {
          this.sections = this.getStaffArray(staff)
          console.log(this.sections)
        }else {
          throw new TypeError('Items of staff must be Object or String')
        }
      },
      
      handleTouchStart(e) {
        if (e.target.tagName !== 'LI') {
          return;
        }
        this.navOffsetX = e.changedTouches[0].clientX;
        this.scrollList(e.changedTouches[0].clientY);
        window.addEventListener('touchmove', this.handleTouchMove);
        window.addEventListener('touchend', this.handleTouchEnd);
      },

      handleTouchMove(e) {
        e.preventDefault();
        this.scrollList(e.changedTouches[0].clientY);
      },

      handleTouchEnd() {
        window.removeEventListener('touchmove', this.handleTouchMove);
        window.removeEventListener('touchend', this.handleTouchEnd);
      },

      scrollList(y) {
        let currentItem = document.elementFromPoint(this.navOffsetX, y);
        if (!currentItem || !currentItem.classList.contains('jc-indexlist__navitem')) {
          return;
        }
        this.$toast({
          message:currentItem.innerText,
          duration:this.duration
        });
        this.$refs['view'+currentItem.innerText][0].scrollIntoView(true);
      }
    },

    mounted() {
      if (!this.currentHeight) {
        window.scrollTo(0, 0);
        this.$nextTick(()=>{
          this.currentHeight = document.documentElement.clientHeight - this.$refs.content.getBoundingClientRect().top;
        });
      }
      this.sort(this.staff);
    }
  });
</script>