<template>
  <a class="jc-tab-item" @click="$parent.$emit('input', id)">
    <div class="jc-item" :class="{ 'is-selected': $parent.value === id }">
      <div class="jc-tab-item-icon">
        <slot name="icon"></slot>
      </div>
      <div class="jc-tab-item-label">
        <slot></slot>
      </div>
    </div>
  </a>
</template>
<script>
import create from 'utils/create';
export default create({
    name: "tab-item",
    props: ["id"]
});
</script>

