import Vue from 'vue';
import VueToast from './src/toast';
import { isObj } from 'utils/util';

const defaultOptions = {
    type: 'text',
    mask: false,
    message: '',
    value: true,
    duration: 3000,
    position: 'middle',
    forbidClick: false,
    hover: true
};

const loadingOptions = {
    type: 'loading',
    forbidClick: true,
    hover: false
};

const parseOptions = message => isObj(message) ? message : { message };

let queue = [];
let singleton = true;

function createInstance() {
    if (!queue.length || !singleton) {
        const toast = new(Vue.extend(VueToast))({
            el: document.createElement('div')
        });
        document.body.appendChild(toast.$el);
        queue.push(toast);
    }
    return queue[queue.length - 1];
}

// transform toast options to popup props
function transformer(options) {
    options.overlay = options.mask;
    return options;
}

function Toast(options = {}) {
    const toast = createInstance();
    options = {
        ...defaultOptions,
        ...parseOptions(options),
        clear() {
            toast.value = false;
        }
    };
    Object.assign(toast, transformer(options));
    clearTimeout(toast.timer);

    if (options.hover && options.duration > 0) {
        toast.timer = setTimeout(() => {
            toast.clear();
        }, options.duration);
    }


    return toast;
}


Toast.loading = options => {
    let temp = Object.assign({}, defaultOptions, loadingOptions, options);
    Toast(temp);
};

Toast.clear = all => {
    if (queue.length) {
        if (all) {
            queue.forEach(toast => {
                toast.clear();
            });
            queue = [];
        } else if (singleton) {
            queue[0].clear();
        } else {
            queue.shift().clear();
        }
    }
};

Toast.allowMultiple = (allow = true) => {
    singleton = !allow;
};

export default Toast;