<template>
    <div class="jc-container" :class="{'jc-container-pad': conPadding}">
        <slot></slot>
    </div>
</template>
<script>
import create from "utils/create";
export default create({
    //组件名称
    name: "container",
    //组件将不会把未被注册的props呈现为普通的HTML属性
    inheritAttrs: false,
    props: {
        conPadding: {
            type: Boolean
        }
    }
});
</script>
