<template>
  <div :class="$namespace +'-progress'">
    <div :class="[
		'progress-' + type,
		{
			'progress-text-outside': textOutside,
			'progress-text-range': range,
		}
		]" cl-gradual>
      <div class="progress-bar" v-if="type === 'line'">
        <div class="progress-bar-outer">
          <div class="progress-bar-inner" :style="barStyle" :class="{'cl-gradual':gradual}">
            <div class="progress-bar-innerText" v-if="showText && textOutside&&!range">{{percentage}}%</div>
            <div class="progress-button" v-if="(showText && textOutside)||range"></div>
          </div>
        </div>
      </div>
      <!--圆形进度条-->
      <div class="progress-circle" :style="{height: width + 'px', width: width + 'px'}" v-else>
        <svg viewBox="0 0 100 100">
          <path class="el-progress-circle__track" :d="trackPath" stroke="#e5e9f2" :stroke-width="relativeStrokeWidth" fill="none"></path>
          <path class="el-progress-circle__path" :d="trackPath" stroke-linecap="round" :stroke="stroke" :stroke-width="relativeStrokeWidth" fill="none" :style="circlePathStyle"></path>
        </svg>
      </div>
      <!--圆形进度条end-->
      <!--进度文字-->
      <div class="progress-text" v-if="showText && !textOutside">
        <template v-if="!range">{{percentage}}%</template>
      </div>
      <!--进度文字end-->
    </div>
    <div v-if="range">
      <div class="marking-l">{{rangeData[0]}}</div>
      <div class="marking-r">{{rangeData[1]}}</div>
    </div>

  </div>
</template>
<script>
import create from "utils/create";
export default create({
    name: "progress",
    props: {
        type: {
            //进度条类型
            type: String,
            default: "line",
            validator: val => ["line", "circle"].indexOf(val) > -1
        },
        percentage: {
            //当前显示比例
            type: Number,
            default: 0,
            validator: val => val >= 0 && val <= 100
        },
        range: Boolean, //区间类型
        rangeData: {
            //区间数据
            type: Array,
            default: function() {
                return [0, 100];
            }
        },
        currentValue: {
            //区间类型中，当前值
            type: Number,
            default: 0
        },
        strokeWidth: {
            //进度条的宽度，暂时只有圆形进度条可用。
            type: Number,
            default: 6
        },
        textOutside: Boolean, //是否在进度条上方显示百分比
        gradual: Boolean, //渐变
        width: {
            //圆形进度条直径
            type: Number,
            default: 126
        },
        showText: {
            //是否显示进度百分比数字
            type: Boolean,
            default: true
        },
        color: {
            //预留，暂时无用
            type: String,
            default: ""
        }
    },
    computed: {
        relativeStrokeWidth() {
            return (this.strokeWidth / this.width * 100).toFixed(1);
        },
        barStyle() {
            //按照百分比，显示当前所处进度的长度
            const style = {};
            if (!this.range) {
                style.width = this.percentage + "%";
            } else {
                var percentageTemp = 0;
                if (this.currentValue <= this.rangeData[0]) percentageTemp = 0;
                else if (this.currentValue >= this.rangeData[1])
                    percentageTemp = 100;
                else {
                    var temp = this.currentValue - this.rangeData[0];
                    percentageTemp =
                        (
                            temp /
                            (this.rangeData[1] - this.rangeData[0])
                        ).toFixed(2) * 100;
                }
                style.width = percentageTemp + "%";
            }
            // style.backgroundColor = this.color;
            return style;
        },
        trackPath() {
            const radius = parseInt(
                50 - parseFloat(this.relativeStrokeWidth) / 2,
                10
            );
            return `M 50 50 m 0 -${radius} a ${radius} ${radius} 0 1 1 0 ${radius *
                2} a ${radius} ${radius} 0 1 1 0 -${radius * 2}`;
        },
        perimeter() {
            const radius = 50 - parseFloat(this.relativeStrokeWidth) / 2;
            return 2 * Math.PI * radius;
        },
        circlePathStyle() {
            const perimeter = this.perimeter;
            return {
                strokeDasharray: `${perimeter}px,${perimeter}px`,
                strokeDashoffset:
                    (1 - this.percentage / 100) * perimeter + "px",
                transition: "stroke-dashoffset 0.6s ease 0s, stroke 0.6s ease"
            };
        },
        stroke() {
            let ret;
            if (this.color) {
                ret = this.color;
            } else {
                ret = "#6997ed";
            }
            return ret;
        }
    }
});
</script>

